export default {
	data() {
		return {
			
		}
	},
	methods: {
		getUserInfo(res){
			//uniapp将原先小程序的返回数据,放在mp属性中
			console.log('getUserInfo',res.detail.userInfo)
			let {userInfo} =res.detail;
			if(userInfo){
				
				uni.reLaunch({
					url:"/pages/personal/personal?userInfo="+JSON.stringify(userInfo)
				})
			}
		}
	}
}
